import Vue from 'vue'; 
import App from './App.vue';  
import router from './router'; 
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';  
 
// 关闭 Vue 在启动时生成的生产提示 
Vue.config.productionTip  = false;
 
// 使用 ElementUI 插件 
Vue.use(ElementUI); 
 
// 导入页面组件并全局注册 
import DDALineRasterization from './views/graphics/DDALineRasterization.vue';  
import SimplePolygonFilling from './views/graphics/SimplePolygonFilling.vue';  
import ComplexCurvesAreaFilling from './views/graphics/ComplexCurvesAreaFilling.vue';  
import ClipLinesWithRectWindow from './views/graphics/ClipLinesWithRectWindow.vue';  
import GraphicTransformationAlgorithms from './views/graphics/GraphicTransformationAlgorithms.vue';  
 
Vue.component('DDALineRasterization',  DDALineRasterization); 
Vue.component('SimplePolygonFilling',  SimplePolygonFilling); 
Vue.component('ComplexCurvesAreaFilling',  ComplexCurvesAreaFilling); 
Vue.component('ClipLinesWithRectWindow',  ClipLinesWithRectWindow); 
Vue.component('GraphicTransformationAlgorithms',  GraphicTransformationAlgorithms); 
 
// 创建 Vue 实例并挂载到 DOM 
new Vue({
  router,
  render: h => h(App)
}).$mount('#app'); 