<template>
    <div>
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>绘制形状及进行缩放和平移</span>
            </div>

            <el-form ref="form" :model="form" label-width="120px">
                <el-form-item label="选择预设:">
                    <el-select v-model="selectedPreset" placeholder="请选择" @change="updateMatrix">
                        <el-option label="缩放" value="scale"></el-option>
                        <el-option label="平移" value="translate"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="输入顶点 (例如: (0,0),(1,1),(1,2),(0,2))：">
                    <el-input v-model="verticesInput" placeholder="输入顶点" @keyup.enter.native="addVertices"></el-input>
                    <el-button type="primary" @click="addVertices">添加顶点</el-button>
                </el-form-item>

                <div v-if="vertices.length > 0">
                    <h3>当前顶点:</h3>
                    <el-table :data="vertices" style="width: 100%">
                        <el-table-column prop="x" label="X"></el-table-column>
                        <el-table-column prop="y" label="Y"></el-table-column>
                    </el-table>
                </div>

                <div v-if="selectedPreset === 'scale'">
                    <h3>缩放矩阵:</h3>
                    <table>
                        <tbody>
                            <tr>
                                <td>
                                    <el-input-number v-model="matrix[0][0]" :disabled="isLocked(0, 0)"
                                        placeholder="x缩放"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[0][1]" :disabled="isLocked(0, 1)"
                                        placeholder="0"></el-input-number>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <el-input-number v-model="matrix[1][0]" :disabled="isLocked(1, 0)"
                                        placeholder="0"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[1][1]" :disabled="isLocked(1, 1)"
                                        placeholder="y缩放"></el-input-number>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <div v-if="selectedPreset === 'translate'">
                    <h3>平移矩阵:</h3>
                    <table>
                        <tbody>
                            <tr>
                                <td>
                                    <el-input-number v-model="matrix[0][0]" :disabled="isLocked(0, 0)"
                                        placeholder="1 (锁定)"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[0][1]" :disabled="isLocked(0, 1)"
                                        placeholder="0 (锁定)"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[0][2]" :disabled="isLocked(0, 2)"
                                        placeholder="平移X"></el-input-number>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <el-input-number v-model="matrix[1][0]" :disabled="isLocked(1, 0)"
                                        placeholder="0 (锁定)"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[1][1]" :disabled="isLocked(1, 1)"
                                        placeholder="1 (锁定)"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[1][2]" :disabled="isLocked(1, 2)"
                                        placeholder="平移Y"></el-input-number>
                                </td>
                            </tr>
                            <tr>
                                <td>
                                    <el-input-number v-model="matrix[2][0]" :disabled="isLocked(2, 0)"
                                        placeholder="1 (锁定)"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[2][1]" :disabled="isLocked(2, 1)"
                                        placeholder="1 (锁定)"></el-input-number>
                                </td>
                                <td>
                                    <el-input-number v-model="matrix[2][2]" :disabled="isLocked(2, 2)"
                                        placeholder="1 (锁定)"></el-input-number>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>

                <el-button type="primary" @click="applyTransformation">应用变换</el-button>

                <div id="plot" ref="plot"></div>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import Plotly from 'plotly.js-dist';

export default {
    data() {
        return {
            selectedPreset: 'scale',  // 默认为缩放  
            vertices: [],  // 存储图形顶点  
            verticesInput: '',  // 用户输入的顶点  
            matrix: [
                [1, 0, 0],  // 矩阵行  
                [0, 1, 0],  // 不使用平移  
                [0, 0, 1]   // 平移向量  
            ]
        };
    },
    methods: {
        addVertices() {
            // 解析用户输入的顶点  
            const input = this.verticesInput.trim();
            const vertexStrings = input.match(/\((-?\d+\.?\d*),\s*(-?\d+\.?\d*)\)/g);
            if (vertexStrings) {
                this.vertices = vertexStrings.map(str => {
                    const coords = str.match(/(-?\d+\.?\d*),\s*(-?\d+\.?\d*)/);
                    return { x: parseFloat(coords[1]), y: parseFloat(coords[2]) };
                });
            } else {
                alert("无效的输入格式！请确保输入格式正确。");
            }
            // this.verticesInput = '';  // 清空输入框  
        },
        updateMatrix() {
            // 根据选定的预设更新矩阵值  
            if (this.selectedPreset === 'scale') {
                this.matrix = [
                    [undefined, 0],
                    [0, undefined]
                ];
            } else {
                this.matrix = [
                    [1, 0, undefined],  // 用于平移  
                    [0, 1, undefined],
                    [0, 0, 1]
                ];
            }
        },
        isLocked(row, col) {
            // 确定输入框是否被锁定  
            if (this.selectedPreset === 'scale') {
                // 缩放预设锁定位置 (1, 2) 和 (2, 1)  
                if ((row === 0 && col === 1) || (row === 1 && col === 0)) return true;
            } else if (this.selectedPreset === 'translate') {
                // 平移预设锁定位置 (0, 0), (0, 1), (1, 0), (1, 1)  
                if ((row === 0 && col !== 2) || (row === 1 && col !== 2) || row === 2) return true;
            }
            return false;
        },
        applyTransformation() {
            const transformedPoints = this.vertices.map(vertex => {
                // 执行矩阵相乘  
                let newX, newY;
                if (this.selectedPreset === 'scale') {
                    newX = vertex.x * this.matrix[0][0]; // x缩放  
                    newY = vertex.y * this.matrix[1][1]; // y缩放  
                } else { // translate  
                    newX = vertex.x + Number(this.matrix[0][2]); // x平移  
                    newY = vertex.y + Number(this.matrix[1][2]); // y平移  
                }
                return { x: newX, y: newY };
            });

            const layout = {
                xaxis: {
                    autorange: true, // 启用自动范围调整 
                    title: 'X轴'
                },
                yaxis: {
                    autorange: true, // 启用自动范围调整 
                    title: 'Y轴'
                }
            };

            // 准备原始图形的x和y坐标数组，并添加第一个点到末尾以形成闭环 
            let originalX = this.vertices.map(p => p.x);
            originalX.push(this.vertices[0].x);
            let originalY = this.vertices.map(p => p.y);
            originalY.push(this.vertices[0].y);

            // 准备变换后图形的x和y坐标数组，并添加第一个点到末尾以形成闭环
            let transformedX = transformedPoints.map(p => p.x);
            transformedX.push(transformedPoints[0].x);
            let transformedY = transformedPoints.map(p => p.y);
            transformedY.push(transformedPoints[0].y);  

            const data = [
                {
                    x: originalX,
                    y: originalY,
                    mode: 'markers+lines',
                    type: 'scatter',
                    name: '原始图形',
                    marker: { color: 'blue' },
                    line: { dash: 'dot' }
                },
                {
                    x: transformedX,
                    y: transformedY,
                    mode: 'markers+lines',
                    type: 'scatter',
                    name: '变换后图形',
                    marker: { color: 'red' }
                }
            ];

            console.log(data[1]);
            Plotly.newPlot(this.$refs.plot, data, layout); // 绘图  
        }
    },
    mounted() {
        this.updateMatrix();  // 初始化矩阵  
    }
};  
</script>

                                        <style>
                                            table {
                                                border-collapse: collapse;
                                                margin: 20px 0;
                                            }

                                            td {
                                                border: 1px solid #ccc;
                                                padding: 8px;
                                                text-align: center;
                                            }

                                            input {
                                                width: 80px;
                                            }
                                        </style>
