<template>
    <el-container class="centered-container">
        <el-header>
            <h2 style="text-align: center;">绘制 sin 和 cos 函数区域</h2>
        </el-header>
        <el-main class="full-height">
            <el-form :model="form" inline>
                <el-form-item label="X 轴范围">
                    <el-input v-model="form.xMin" placeholder="最小值" type="number" style="width: 120px;"></el-input>
                    <el-input v-model="form.xMax" placeholder="最大值" type="number" style="width: 120px;"></el-input>
                </el-form-item>
                <el-form-item label="采样点个数">
                    <el-input v-model="form.numPoints" type="number" placeholder="输入正整数"
                        style="width: 120px;"></el-input>
                </el-form-item>
                <el-form-item label="填充方式">
                    <el-radio-group v-model="form.fillMethod">
                        <el-radio label="sin > cos">sin > cos</el-radio>
                        <el-radio label="sin < cos">sin &lt; cos</el-radio>
                        <el-radio label="sin 或 cos">以上两者</el-radio>
                    </el-radio-group>
                    <el-button type="primary" @click="drawPlot" style="margin-left: 20px;">绘制图形</el-button>
                </el-form-item>
            </el-form>
            <div id="plotly-chart" style="width: 100%; height: 400px; margin-top: 20px;"></div>
            <el-alert v-if="errorMessage" title="错误" type="error" :description="errorMessage"
                style="margin-top: 10px;"></el-alert>
        </el-main>
    </el-container>
</template>
 
<script>
import Plotly from 'plotly.js-dist';

export default {
    data() {
        return {
            form: {
                xMin: 0,
                xMax: 10,
                numPoints: 100,
                fillMethod: 'sin > cos',
            },
            errorMessage: '',
        };
    },
    methods: {
        validateInputs() {
            const { xMin, xMax, numPoints } = this.form;
            if (xMin >= xMax) {
                this.errorMessage = 'X 轴最小值必须小于最大值。';
                return false;
            }
            if (numPoints <= 0) {
                this.errorMessage = '采样点个数必须为正整数。';
                return false;
            }
            this.errorMessage = '';
            return true;
        },
        drawPlot() {
            if (!this.validateInputs()) return;

            const { xMin, xMax, numPoints, fillMethod } = this.form;
            const x = Array.from({ length: numPoints }, (_, i) => xMin + (xMax - xMin) * i / (numPoints - 1));
            const y1 = x.map(val => Math.sin(val));
            const y2 = x.map(val => Math.cos(val));

            let fillY;
            if (fillMethod === 'sin > cos') {
                fillY = y1.map((val, i) => Math.max(val, y2[i]));
            } else if (fillMethod === 'sin < cos') {
                fillY = y1.map((val, i) => Math.min(val, y2[i]));
            } else {
                fillY = y1.concat(y2);
            }

            const layout = {
                title: `X in [${xMin}, ${xMax}]`,
                xaxis: {
                    title: 'x'
                },
                yaxis: {
                    title: 'y'
                }
            };

            Plotly.newPlot('plotly-chart', [
                { x, y: y1, name: 'sin(x)', type: 'scatter', mode: 'lines' },
                { x, y: y2, name: 'cos(x)', type: 'scatter', mode: 'lines' },
                {
                    x,
                    y: fillY,
                    name: 'Fill',
                    type: 'scatter',
                    mode: 'none',
                    fill: 'tonexty',
                    fillcolor: 'green',
                    opacity: 0.5
                }
            ], layout);
        }
    },
};
</script>
 
<style scoped>
.centered-container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.full-height {
    flex: 1;
}

/* 使 el-form 内的元素水平排列 */
.el-form--inline .el-form-item {
    display: inline-block;
    margin-right: 20px;
}

/* 调整 Plotly 图表容器的样式 */
#plotly-chart {
    width: 100%;
    /* 确保图表宽度填满容器 */
    height: 400px;
    /* 可以根据需要调整高度 */
    margin-top: 20px;
}
</style>