<template>
    <el-container class="centered-container" style="width: 100%">
        <el-header>
            <h1 style="margin: 0; text-align: center;">线段裁剪工具</h1> <!-- 使标题居中 -->
        </el-header>
        <el-main>
            <el-form ref="lineForm" :model="lineForm" label-width="120px">
                <el-row>
                    <el-form-item label="线段起点 (x1, y1):">
                        <el-col :span="11">
                            <el-input type="number" v-model.number="lineForm.x1" placeholder="x1"></el-input>
                        </el-col>
                        <el-col :span="11" :offset="2">
                            <el-input type="number" v-model.number="lineForm.y1" placeholder="y1"></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="线段终点 (x2, y2):">
                        <el-col :span="11">
                            <el-input type="number" v-model.number="lineForm.x2" placeholder="x2"></el-input>
                        </el-col>
                        <el-col :span="11" :offset="2">
                            <el-input type="number" v-model.number="lineForm.y2" placeholder="y2"></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="裁剪窗口左下 (xl, yb):">
                        <el-col :span="11">
                            <el-input type="number" v-model.number="lineForm.xl" placeholder="xl"></el-input>
                        </el-col>
                        <el-col :span="11" :offset="2">
                            <el-input type="number" v-model.number="lineForm.yb" placeholder="yb"></el-input>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="裁剪窗口右上 (xr, yt):">
                        <el-col :span="11">
                            <el-input type="number" v-model.number="lineForm.xr" placeholder="xr"></el-input>
                        </el-col>
                        <el-col :span="11" :offset="2">
                            <el-input type="number" v-model.number="lineForm.yt" placeholder="yt"></el-input>
                        </el-col>
                    </el-form-item>
                    <el-button type="primary" @click="draw">裁剪并绘制线段</el-button>
                </el-row>
            </el-form>
            <canvas ref="canvas" width="800" height="500" style="border: 1px solid black;"></canvas>
        </el-main>
    </el-container>
</template>

<script>
export default {
    data() {
        return {
            lineForm: {
                x1: 0,
                y1: 450,
                x2: 700,
                y2: 50,
                xl: 100,
                xr: 700,
                yb: 150,
                yt: 350,
            }
        };
    },
    methods: {
        encode(x, y) {
            let c = 0;
            if (x < this.lineForm.xl) c |= 1; // LEFT 
            if (x > this.lineForm.xr) c |= 2; // RIGHT 
            if (y < this.lineForm.yb) c |= 4; // BOTTOM 
            if (y > this.lineForm.yt) c |= 8; // TOP 
            return c;
        },
        CohenSutherland(x1, y1, x2, y2) {
            const LEFT = 1, RIGHT = 2, BOTTOM = 4, TOP = 8;
            let code1 = this.encode(x1, y1);
            let code2 = this.encode(x2, y2);
            let outcode = code1;
            let x = 0, y = 0;
            let area = false;

            //下面的注释可以防止报错
            // eslint-disable-next-line no-constant-condition
            while (true) {
                // 检查线段完全在裁剪区域内  
                if ((code2 | code1) === 0) {
                    area = true;
                    break;
                }
                // 检查线段完全在裁剪区域外  
                if ((code1 & code2) !== 0) break;
                // 确定需要处理哪个端点  
                if (code1 === 0) outcode = code2;
                else outcode = code1; // 确保每次都是处理当前的outcode  

                // 计算交点  
                if ((LEFT & outcode) !== 0) {
                    x = this.lineForm.xl;
                    y = y1 + (y2 - y1) * (this.lineForm.xl - x1) / (x2 - x1);
                } else if ((RIGHT & outcode) !== 0) {
                    x = this.lineForm.xr;
                    y = y1 + (y2 - y1) * (this.lineForm.xr - x1) / (x2 - x1);
                } else if ((BOTTOM & outcode) !== 0) {
                    y = this.lineForm.yb;
                    x = x1 + (x2 - x1) * (this.lineForm.yb - y1) / (y2 - y1);
                } else if ((TOP & outcode) !== 0) {
                    y = this.lineForm.yt;
                    x = x1 + (x2 - x1) * (this.lineForm.yt - y1) / (y2 - y1);
                }

                // 解决舍入带来的精度问题  
                x = Math.round(x);
                y = Math.round(y);

                // 更新对应的端点  
                if (outcode === code1) {
                    x1 = x;
                    y1 = y;
                    code1 = this.encode(x1, y1);
                } else {
                    x2 = x;
                    y2 = y;
                    code2 = this.encode(x2, y2);
                }

                // 输出当前状态，便于调试  
                console.log(`x=${x}, y=${y}`);
                console.log(`x1=${x1}, y1=${y1}, x2=${x2}, y2=${y2}`);
                console.log(`Loop  start: code1=${code1.toString(2)},  code2=${code2.toString(2)},  outcode=${outcode.toString(2)}`);
            }

            // 返回裁剪后的坐标  
            if (area) return { x1, y1, x2, y2 };
            return null;
        },
        draw() {
            const canvas = this.$refs.canvas;
            const ctx = canvas.getContext('2d');
            ctx.clearRect(0, 0, canvas.width, canvas.height);

            // 转换坐标，使(0, 0)位于左下角
            const convertY = (y) => canvas.height - y;

            // 绘制裁剪窗口 
            ctx.strokeStyle = 'red'; // 更改为红色以便更显眼
            ctx.strokeRect(this.lineForm.xl, convertY(this.lineForm.yb), this.lineForm.xr - this.lineForm.xl, this.lineForm.yb - this.lineForm.yt);

            // 裁剪线段
            const result = this.CohenSutherland(this.lineForm.x1, this.lineForm.y1, this.lineForm.x2, this.lineForm.y2);
            console.log(result);
            if (result) {
                // 绘制裁剪后的线段
                ctx.strokeStyle = 'yellow';
                ctx.beginPath();
                ctx.moveTo(result.x1, convertY(result.y1));
                ctx.lineTo(result.x2, convertY(result.y2));
                ctx.stroke();
            }

            // 绘制原始线段（可选）
            ctx.strokeStyle = 'lightgray';
            ctx.beginPath();
            ctx.moveTo(this.lineForm.x1, convertY(this.lineForm.y1));
            ctx.lineTo(this.lineForm.x2, convertY(this.lineForm.y2));
            ctx.stroke();
        },
    },
};
</script>

<style scoped>
.centered-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    /* 可选：让容器填满视口高度 */
}
</style>