<template>
    <el-container class="centered-container">
        <el-header>
            <h2 style="text-align: center;">输入多边形的顶点坐标（逆时针顺序）</h2>
        </el-header>
        <el-main>
            <el-form class="input-container" label-width="100px">
                <el-row :gutter="20">
                    <el-col v-for="(point, index) in points" :key="index" :span="12">
                        <el-form-item :label="point.name">
                            <el-input type="number" v-model="point.x" placeholder="x" @blur="validateInput(point)" />
                            <el-input type="number" v-model="point.y" placeholder="y" @blur="validateInput(point)" />
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-button type="primary" @click="drawPolygon" style="margin-top: 20px;">绘制多边形</el-button>
            </el-form>
            <div id="plotly-chart" style="width: 100%; height: 400px; margin-top: 20px;"></div>
            <el-alert v-if="errorMessage" title="错误" type="error" :description="errorMessage"
                style="margin-top: 10px;"></el-alert>
        </el-main>
    </el-container>
</template>

<script>
import Plotly from 'plotly.js-dist';

export default {
    data() {
        return {
            points: [
                { name: '左下角', x: 1, y: 1 },
                { name: '右下角', x: 2, y: 1 },
                { name: '右上角', x: 2, y: 2 },
                { name: '左上角', x: 1, y: 5 },
            ],
            errorMessage: '',
        };
    },
    methods: {
        validateInput(point) {
            if (point.x === null || point.y === null) {
                this.errorMessage = '请输入所有坐标点。';
            } else {
                this.errorMessage = '';
            }
        },
        drawPolygon() {
            const xValues = this.points.map(point => parseFloat(point.x));
            const yValues = this.points.map(point => parseFloat(point.y));

            // 验证所有坐标是否有效  
            if (xValues.includes(NaN) || yValues.includes(NaN)) {
                this.errorMessage = '所有坐标点必须为有效数字。';
                return;
            }
            if (new Set(xValues).size < 2 || new Set(yValues).size < 2) {
                this.errorMessage = '坐标点必须形成一个有效的多边形。';
                return;
            }

            const minX = Math.min(...xValues);
            const maxX = Math.max(...xValues);
            const minY = Math.min(...yValues);
            const maxY = Math.max(...yValues);

            this.errorMessage = '';

            // 创建 Plotly 数据与布局  
            const plotData = [{
                x: [...xValues, xValues[0]], // 闭合多边形  
                y: [...yValues, yValues[0]], // 闭合多边形  
                fill: 'tozeroy',
                type: 'scatter',
                mode: 'lines+markers',
                fillcolor: 'rgba(0, 128, 0, 0.3)', // 半透明填充色  
                line: { color: 'green' },
            }];

            const layout = {
                title: '多边形绘制',
                xaxis: {
                    title: 'X 轴',
                    range: [minX - 1, maxX + 1],
                },
                yaxis: {
                    title: 'Y 轴',
                    range: [minY - 1, maxY + 1],
                },
                showlegend: false,
            };

            // 使用 Plotly 绘制  
            Plotly.newPlot('plotly-chart', plotData, layout,{ staticPlot: true });
        }
    },
};  
</script>

<style scoped>
.centered-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    /* 可选：让容器填满视口高度 */
}

.input-container {
    margin-bottom: 20px;
}
</style>