<template>
    <el-container class="centered-container"> <!-- 添加居中类 -->
        <el-header>
            <h1 style="margin: 0; text-align: center;">DDA Line Rasterization Visualization</h1> <!-- 使标题居中 -->
        </el-header>
        <el-main>
            <el-row :gutter="20" class="centered-row"> <!-- 添加类以居中行 -->
                <el-col :span="6">
                    <el-input type="number" v-model.number="x1" placeholder="X1" clearable />
                </el-col>
                <el-col :span="6">
                    <el-input type="number" v-model.number="y1" placeholder="Y1" clearable />
                </el-col>
                <el-col :span="6">
                    <el-input type="number" v-model.number="x2" placeholder="X2" clearable />
                </el-col>
                <el-col :span="6">
                    <el-input type="number" v-model.number="y2" placeholder="Y2" clearable />
                </el-col>
            </el-row>
            <el-row :gutter="20" style="margin-top: 20px;" class="centered-row"> <!-- 添加类以居中行 -->
                <el-col :span="6">
                    <el-input type="number" v-model.number="canvasSquares" placeholder="Canvas Squares" min="1"
                        clearable />
                </el-col>
                <el-col :span="6">
                    <el-button type="primary" @click="drawLine">Draw Line</el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24" class="centered-canvas"> <!-- 添加类以居中画布 -->
                    <canvas ref="canvas" :width="canvasWidth" :height="canvasHeight"
                        style="border: 1px solid black; display: block; margin-top: 20px;"></canvas>
                </el-col>
            </el-row>
        </el-main>
    </el-container>
</template>

<script>
export default {
    data() {
        return {
            x1: 0,
            y1: 0,
            x2: 10,
            y2: 3,
            canvasSquares: 10, // Number of squares along one dimension of the canvas  
            canvasWidthPixels: 400, // Fixed canvas width in pixels   
            canvasHeightPixels: 400, // Fixed canvas height in pixels   
        };
    },
    computed: {
        squareSize() {
            return this.canvasWidthPixels / this.canvasSquares;
        },
        canvasWidth() {
            return this.canvasWidthPixels;
        },
        canvasHeight() {
            return this.canvasHeightPixels;
        },
        dx() {
            return this.x2 - this.x1;
        },
        dy() {
            return this.y2 - this.y1;
        },
        steps() {
            return Math.max(Math.abs(this.dx), Math.abs(this.dy));
        },
        xIncrement() {
            return this.dx / this.steps;
        },
        yIncrement() {
            return this.dy / this.steps;
        },
    },
    mounted() {
        this.context = this.$refs.canvas.getContext('2d');
        this.drawGrid(); // Draw the grid on mounted  
    },
    methods: {
        drawGrid() {
            const context = this.context;
            context.clearRect(0, 0, this.canvasWidth, this.canvasHeight);
            context.strokeStyle = 'black';

            for (let i = 0; i <= this.canvasSquares; i++) {
                context.beginPath();
                context.moveTo(i * this.squareSize, 0);
                context.lineTo(i * this.squareSize, this.canvasHeight);
                context.stroke();

                context.beginPath();
                context.moveTo(0, i * this.squareSize);
                context.lineTo(this.canvasWidth, i * this.squareSize);
                context.stroke();
            }
        },
        drawLine() {
            const context = this.context;
            this.drawGrid();

            let x = this.x1;
            let y = this.y1;

            context.fillStyle = 'blue';

            for (let i = 0; i < 2 * Math.max(this.canvasWidthPixels, this.canvasHeightPixels); i++) {
                const gridX = Math.floor(x);
                const gridY = this.canvasSquares - 1 - Math.floor(y);

                if (gridX >= 0 && gridX < this.canvasSquares && gridY >= 0 && gridY < this.canvasSquares) {
                    context.fillRect(gridX * this.squareSize, gridY * this.squareSize, this.squareSize, this.squareSize);
                }

                x += this.xIncrement;
                y += this.yIncrement;
            }
        },
    },
};  
</script>

<style>
.centered-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    /* Optional: make container fill the viewport height */
}

.centered-row {
    display: flex;
    justify-content: center;
    /* Center row contents */
}

.centered-canvas {
    display: flex;
    justify-content: center;
    /* Center the canvas */
}
</style>